import React, { useRef } from "react";
import { ProfileData } from "@/app/utility/props";
import { UploadCloud } from "lucide-react";

interface Props {
  profile: ProfileData;
  setProfile: React.Dispatch<React.SetStateAction<ProfileData>>;
}

export default function ResumeUpload({ profile, setProfile }: Props) {
  const fileRef = useRef<HTMLInputElement | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) setProfile({ ...profile, resume: file.name });
  };

  return (
    <div className="bg-gray-50 p-6 rounded-xl border border-gray-200 shadow-sm">
      <h2 className="text-xl font-semibold text-gray-800 mb-4">Resume</h2>

      <div className="flex items-center justify-between bg-white border border-gray-300 rounded-xl p-5 shadow-sm">
        <div>
          <p className="text-gray-700 font-medium">
            {profile.resume ? "Uploaded Resume:" : "No resume uploaded"}
          </p>
          {profile.resume && (
            <p className="text-gray-500 text-sm mt-1">{profile.resume}</p>
          )}
        </div>

        <button
          type="button"
          onClick={() => fileRef.current?.click()}
          className="flex items-center gap-2 bg-blue-600 text-white px-5 py-3 rounded-xl hover:bg-blue-700 transition-all"
        >
          <UploadCloud size={18} />
          {profile.resume ? "Replace Resume" : "Upload Resume"}
        </button>
      </div>

      <input
        ref={fileRef}
        type="file"
        accept=".pdf"
        className="hidden"
        onChange={handleFileChange}
      />

      <p className="text-sm text-gray-500 mt-2">
        Only PDF files allowed (max 5MB).
      </p>
    </div>
  );
}
